@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);


.button{padding:0.5rem 0.8rem;border-radius:6px;box-shadow:0px 2px 3px 0px rgba(51,51,51,0.2);border:none;color:#3f3f3f;background-color:#e0e0e0}.button:hover,.button:focus{background:#aeaeae}.outline{box-shadow:none;border:1px solid #3d5afe;color:#3d5afe;background-color:transparent}.outline:hover,.outline:focus{background-color:rgba(41,98,255,0.1)}.text{box-shadow:none;border:none;color:#3d5afe;background-color:transparent}.text:hover,.text:focus{background-color:rgba(41,98,255,0.1)}.disabledShadow,.startIcon,.endIcon{border:none;color:#fff;background:#3d5afe}.disabledShadow:hover,.disabledShadow:focus,.startIcon:hover,.startIcon:focus,.endIcon:hover,.endIcon:focus{background:#3d5afe}.disabledShadow{box-shadow:none !important}.startIcon,.endIcon{box-shadow:0px 2px 3px 0px rgba(61,90,254,0.2)}.disabled{box-shadow:none !important;border:none !important;color:#9e9e9e !important;background:#e0e0e0 !important}.disabled:hover,.disabled:focus{background:#e0e0e0 !important}.text.disabled{box-shadow:none;border:none;color:#9e9e9e;background:none}.text.disabled:hover,.text.disabled:focus{background:none}.material-icons{font-size:18px}.material-icons,.btn__text{vertical-align:middle}.start{margin-right:0.4rem}.end{margin-left:0.4rem}.primary{background:#2962ff;color:#fff}.primary:hover,.primary:focus{color:#fff;background:#0039cb}.secondary{background:#455a64;color:#fff}.secondary:hover,.secondary:focus{color:#fff;background:#1c313a}.default{background-color:#e0e0e0;color:#3f3f3f}.default:hover,.default:focus{background:#aeaeae;color:#3f3f3f}.sm{font-size:10px;padding:2px 5px}.md{font-size:14px;padding:0.4rem 0.8rem}.lg{font-size:18px;padding:0.7rem 1.5rem}

.line{display:flex;justify-content:space-evenly;margin-bottom:2rem}

* {
  padding: 0;
  margin: 0;
}

html {
  font-size: 14px;
}
body {
  font-family: "Noto Sans JP", sans-serif;
  box-sizing: border-box;
  text-align: justify;
  padding: 5rem;
}

h3 {
  margin-bottom: 3rem;
  /* text-decoration: underline;
  text-decoration-color: yellow; */
  color: white;
  font-size: 1rem;
  border: 1px solid red;
  display: inline-block;
  padding: 5px;
  background-color: black;
}
.user {
  font-family: "Montserrat";
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 17px;
  letter-spacing: 0em;
  text-align: center;
  margin-top: 13rem;
}

